import { Glisy,Addgroup } from '@/api/index'
export default {
  data () {
    return {
      value: '',
      list: [],
      dont: true,
      headName:'查找群组'
    }
  },
  methods: {
    onSearch() {
      Glisy({pageNo: '1', pageSize: '10', keywords: this.value}).then( res => {
          if(res.code == 1 ) {
            console.log(res,53)
              if( res.data.list.length ) {
                 this.list = res.data.list
                 this.dont = true
              } else {
                 this.dont = false
              }

          }
      })
    },
    addGroups(val) {
      console.log(1111)
       Addgroup({groId:val.groId}).then( res => {
         console.log(res,61)
          if(res.code == 403 ) {
                 this.onSearch()
          }
       })
    }
  },
  created () {
    this.onSearch()
  },
  components: {

  }
}



