
import Vue from 'vue'
import App from './App'
import router from './router/router'
import axios from './axios/index'
Vue.prototype.$axios = axios

import Vant from 'vant'
import 'vant/lib/index.css'
Vue.use(Vant)

import clocklist from '@/components/clockList'
import popup from '@/components/popup'
import head from '@/components/headgoto'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import Group from 'vux/src/components/group/index.vue'

Vue.component('inline-calendar', InlineCalendar)
Vue.component('x-input', XInput)
Vue.component('datetime', Datetime)
Vue.component('x-textarea', XTextarea)
Vue.component('x-button', XButton)
Vue.component('group', Group)
Vue.component('v-clock', clocklist)
Vue.component('v-popup', popup)
Vue.component('v-head', head)
Vue.use(AlertPlugin)
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'
Vue.use(Mint);

import { queryList } from "./api/index"
import util from "./util/util"
router.beforeEach((to, from, next) => {
let code = util.getCode('code')
if (!code) {


next()
} else {
var auth = localStorage.getItem('Authorization');

console.log(to.fullPath);
if(!auth||to.fullPath=='/home'){
queryList({ code: code }).then(res => {
if ( res.code == 1 ) {
localStorage.setItem('Authorization', res.data.token)
if(res.data.isFirst==0){

next()
}
if(res.data.isFirst==1){

sessionStorage.setItem('tabNum','2');
next('nav/card')
}
if(res.data.isFirst==2){

sessionStorage.setItem('tabNum','0');
next('nav/friend')
}
}
})

}else{
next();
}







}
})
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



