import { recordPer , pushCard, groups } from '@/api/index'
export default {
  data () {
    return {
      books:[],
      // booksss:[],
      num:'',
      bookss:[],
      pushCount: {},  //积善行
      volunteer: '', // 立志愿
      classic: '',  // 经典名句
      practices: { character: '' ,work: '', family: ''},  //行～实践
      introspective: '', //省省悟
      thanks: '', //感谢
      btnTitle:'',
      first:false
    }
  },
  methods: {
    gainPer() {
       recordPer({}).then( res => {
         console.log(res,157)
          if(res.code == 1 || res.code == 402 ) {
             console.log(res,158)
              if(res.code == 402 ) {
                 this.btnTitle = '今日已打卡, 请勿重复提交'
                 this.first = true
              } else {
                 this.btnTitle = '提交'
              }
              this.thanks = res.data.thanks
              this.practice = res.data.practice ? res.data.practice : this.practices
              this.classic = res.data.classic
              this.introspective = res.data.introspective
              this.bookss = res.data.books
              this.pushCount = res.data.pushCount
              this.volunteer = res.data.volunteer
          }
       })
    },
    punch(){
      if(this.btnTitle == '今日已打卡, 请勿重复提交') {
         return false
      }
      pushCard({thanks:this.thanks,practice:this.practices,books:this.bookss,classic:this.classic,introspective:this.introspective,volunteer:this.volunteer,pushCount:this.pushCount,isPub:'1',bookish:this.books}).then( res => {
           if(res.code == 1) {
              this.$router.push({path:'/nav/friend'})
           }
      })
    },
    cardBook () {
      let data={title:'',readCount:1,readTotal:0}
       this.books.push(data)
    },
    cardDel(i) {
       this.books.splice(i,1)
    },
    bblur() {
      window.scroll(0,0);
    },
  },
  created () {
     this.gainPer()
  },
  components: {
  },
    mounted() {
    window.addEventListener('resize', () => {
    const activeElement = document.activeElement
    if (activeElement.tagName === 'INPUT' || activeElement.tagName === 'TEXTAREA') {
        setTimeout(() => {
          // activeElement.scrollIntoView({block:'start'})
          // window.scroll(0,0);
          activeElement.scrollIntoViewIfNeeded()

        }, 0)
      }
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', () => {
    const activeElement = document.activeElement
    if (activeElement.tagName === 'INPUT' || activeElement.tagName === 'TEXTAREA') {
        setTimeout(() => {
          // activeElement.scrollIntoView({block:'start'})
          // window.scroll(0,0);
          activeElement.scrollIntoViewIfNeeded()

        }, 0)
      }
    })
  },

}



