import { Parise } from '@/api/index'
export default {
  name: 'clock', // 结构名称
  data() {
    return {
      // 数据模型
    }
  },
  props: {
    // 集成父级参数
    clocklist:{
      type:Array
    },
    name:{
      type:String,
      default:sessionStorage.getItem('userName')
    },
  },
  methods: {
    // 方法 集合
    fullClick(i){
      let obj=this.clocklist[i];
      if(obj.fullTxt=='收起'){
        obj.isFull=false;
        obj.fullTxt='全文';
      }else{
        obj.isFull=true;
        obj.fullTxt='收起';
      }
      this.$set(this.clocklist,i,obj);
    },
    isPariseFn(i){
      if(this.name==''){
        return false
      }
      let id=this.clocklist[i].pkid;
      let that=this;
      Parise({logId:id}).then(res =>{
        let obj=that.clocklist[i];
        obj.isParise=true;
        if(res.msg=='操作成功'){
          if(that.strInArr(obj.praise)){
            obj.praise.push(that.name);
          }
          that.$set(that.clocklist,i,obj);
        }else{
          alert('因网络原因，点赞失败')
          obj.isParise=false;
        }
    })
    },
    strInArr(arr){
      if(arr.length==0){
        return true
      }
      for(let x of arr){
        if(x==this.name){
          return false
        }else{
          return true
        }
      }
    },
  }

}




