import { Personage, Saveuser } from '@/api/index'
export default {
  data () {
    return {
       layout:false,
      //  username: '',
      //  mobile: '',
      //  company: '',
      //  post: '',
       user:[],
       bookss: [],
       books:[{}],
       values:'',
       Blength:0,
       pushCount:[],
       Number: true,
       delay:true
    }
  },
  methods: {
      textM() {
      window.scroll(0,0)
      var myreg= /^[1][3,4,5,6,7,8,9][0-9]{9}$/
      if( myreg.test(this.user.phone)) {
          this.$refs.Moblie.style.color = '#000'
          this.Number = true
      } else {
          this.$refs.Moblie.style.color = 'red'
          this.Number = false
          this.layout = true
          this.verify()
      }
    },
    verify() {
       setTimeout(() => {
          this.layout = false
       }, 1500);
    },
    bblur() {
      window.scroll(0,0);
    },
    cardDel(i) {
       this.books.splice(i,1)
    },
    cardBook () {
       this.books.push({})
    },
    record() {
          this.pass = true
         if (!this.user.phone && !this.Number ) {
           this.pass = false
          return this.$vux.alert.show({
                  title: '请输入必填选项',
                  content: '请输入正确的手机格式',
                })
         } 
         if(!this.user.name) {
          this.pass = false
           return this.$vux.alert.show({
                  title: '请输入必填选项',
                  content: '请输入您的姓名',
                })
        }
        this.books.forEach( el => {
              var arr = Object.keys(el)
               if(  arr.length != 3 ) {
                  this.pass = false;
                     return  this.$vux.alert.show({
                          title: '请输入必填选项',
                          content: '请填写或者删除多余空白书本',
                   })
               }
        });
         this.delay = true
        if( this.pass && this.delay ) {
           this.delay = false
           Saveuser({books:this.bookss,user:this.user,pushCount:this.pushCount,volunteer:this.values}).then( res => {
              if(res.code ==1) {
                 this.delay = true
                 return  this.$vux.alert.show({
                          title: '保存成功',
                         })
              }
           })
        }

    },
    gainUser() {
      Personage({}).then( res => {
         if(res.code == 1 ) {
            // this.username = res.data.user.name
            // this.mobile = res.data.user.phone
            // this.company = res.data.user.company
            // this.post = res.data.user.post
            this.user = res.data.user
            this.bookss = res.data.books
            this.Blength = res.data.books.length + 1
            this.pushCount = res.data.pushCount
            this.values = res.data.volunteer
         }
      })
    }
  },
  created () {
     this.gainUser()
  },
  components: {
  }
}



