import { CardRecord,groupUser,groups } from '@/api/index'
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      groupName:'',//群名
      type:'',
      list:[],
      groId:'',//群id
      mask:false,
      thisNum:'',
      tipTxt:'',
    }
  },
  name:'groupUser',
  watch:{

  },
  methods: {
    jumpFriend(i){
      this.$router.push({
        path:'/nav/friend',
        query:{
          id:this.list[i].pkid
        }
      })
    },
    //确定
    maskFn(){
      let i=this.thisNum;
      if(this.tipTxt=='确认要删除该家人？'){
        let data={
          groId:this.groId,
          user:this.list[i].pkid
        }
        this.list.splice(i,1);
        groupUser.removeUser(data).then(res =>{
          if(res.code==1){
            this.$toast('删除成功');
          }
        })
      }else if(this.tipTxt=='确认要转让该群组？'){
        let data={
          groId:this.groId,
          invite:this.list[i].pkid
        }
        groupUser.changeGrouper(data).then(res =>{
          if(res.code==1){
            this.$toast('转让成功');
            this.type=false;
          }
        })
      }
    },
    deleteFn(i){
      this.thisNum=i;
      this.tipTxt='确认要删除该家人？'
      this.mask=true;
    },
    turnFn(i){
      this.thisNum=i;
      this.tipTxt='确认要转让该群组？';
      this.mask=true;
    }
  },
  created () {
    this.groupName=this.$route.query.name;
    this.type=this.$route.query.type;
    this.groId=this.$route.query.id;
    let that=this;
    let data={
      pageNo:1,
      pageSize:100,
      groId:this.$route.query.id,//
    }
    CardRecord.groupPerson(data).then(res =>{
      if(res){
        that.list=res.data.list
      }
    })
  },
  components: {

  },
  mounted(){

  },
  destroyed(){
    groups({}).then( res => {
      let arr=[];
      arr=res.data.create.concat(res.data.join);
      sessionStorage.setItem('groupList',JSON.stringify(arr));
    })
  }
}



